import React, { useEffect, useRef, useState } from "react";
import { Popup } from "@progress/kendo-react-popup";
import { Picker } from "emoji-mart";

import "emoji-mart/css/emoji-mart.css";

const EmojiPicker = ({ onPick }) => {
  const [open, setOpen] = useState(false);

  const anchor = useRef(null);

  useEffect(() => {
    const chatContainer = document.getElementById("chatContainer");
    chatContainer.addEventListener("click", (event) => {
      setOpen(false);
    });

    return () => {
      const chatContainer = document.getElementById("chatContainer");
      chatContainer.removeEventListener("click", () => {});
    }
  }, []);

  const stopEventPropagation = (event) => event.stopPropagation();

  const toggleTemplatesDrawer = () => setOpen(!open);

  const onPickEmoji = (emoji, event) => {
    onPick(emoji);
    event.stopPropagation();
  };

  return (
    <>
      <button
        className={"k-button k-flat k-button-icon"}
        onClick={toggleTemplatesDrawer}
        ref={anchor}
      >
        <span
          className={"k-icon k-i-fav-outline"}
          style={{ fontSize: "20px" }}
        />
      </button>

      <Popup
        anchor={anchor.current}
        anchorAlign={{
          horizontal: "right",
          vertical: "top",
        }}
        popupAlign={{
          horizontal: "right",
          vertical: "bottom",
        }}
        collision={{
          horizontal: "fit",
          vertical: "flip",
        }}
        show={open}
        popupClass={"emoji-picker-pop"}
        animate={false}
      >
        <div className="emoji-picker-container" onClick={stopEventPropagation}>
          <Picker
            set="apple"
            title=""
            onClick={onPickEmoji}
            showPreview={false}
            showSkinTones={false}
            color="#7269ef"
            native={true}
            perLine={6}
          />
        </div>
      </Popup>
    </>
  );
};

export default EmojiPicker;
