let activeConversation = null;
/**
 * Initializes the Twilio Conversation.
 * @param token 
 */
export const initConversation = async (token, callback) => {
  console.log(">>> Initializing Twilio conversation.");

  const conversationsClient = await window.Twilio.Conversations.Client.create(token);

  if(!conversationsClient) {
    callback(false);
    return;
  }
  
  conversationsClient.on("connectionStateChanged", (state) => {
    if (state === "connecting") {
      console.log(">>> Connecting to Twilio.");
    }
    if (state === "connected") {
      console.log(">>> You are connected.");
    }
    if (state === "disconnecting") {
      console.log(">>> Disconnecting from Twilio.");
    }
    if (state === "disconnected") {
      console.log(">>> Disconnected.");
    }
    if (state === "denied") {
      console.log(">>> Failed to connect.");
    }
  });

  conversationsClient.on("conversationLeft", (conversation) => {
    console.log(">>> Left.");
  });

  conversationsClient.on("conversationJoined", (conversation) => {
    console.log(">>> Joined.");
    activeConversation = conversation;
    callback(true);
  });

  // try {
  // } catch (e) {
  //   throw e;
  // }
};

/**
 * Loads the chat history.
 * @param conversation 
 */
export const loadConversation = async () => {
  try {
    console.log(">>> Fetching messages.");
    const res = await activeConversation.getMessages();
    
    return res;
  } catch (e) {
    throw e;
  }
}

/**
 * Starts listening to new messages.
 */
export const startMessageAdded = async (callback) => {
  console.log(">>> Starting message added listener.");

  activeConversation.on("messageAdded", (m) => {
    console.log(">>> New message added.");
    console.log(m);
    callback(m);
  });
}

/**
 * Sends a messages.
 */
 export const sendMessage = async (text) => {
  activeConversation.sendMessage(text);
}