const getAccessToken = async () => {
  try {
    const dentistId = window.dentistId;
    // const dentistId = 2;
    const response = await axiosInstance.post("https://localhost:5001/api/client/anonymouse/" + dentistId);
    return response;
  } catch (e) {
    throw e;
  }
};



export { getAccessToken };
