import React, { useState, useEffect, useLayoutEffect, useRef } from "react";
import { Chat } from "@progress/kendo-react-conversational-ui";

import ChatHeader from "./chat-header";

import { getAccessToken } from "./chat";
import { initConversation, loadConversation, sendMessage, startMessageAdded } from "./twilio-service";
import { mapToChat } from "./helpers/data-mapper";

import crossIc from "./assets/cross.svg";
import chatIc from "./assets/speech-bubble.svg";
import Loading from "./loading";
import EmojiPicker from "./emoji-picker";

const WidgetStatus = Object.freeze({
  LOADING: 1,
  LOADED: 2,
});

function App() {
  const [status, setStatus] = useState(null);
  const [chatWindowOpen, setChatWindowOpen] = useState(false);
  const [chat, setChat] = useState(null);
  const [messages, setMessages] = useState([]);

  const messageRef = useRef(messages);
  const chatIcRef = useRef();
  const closeIcRef = useRef();
  const chatRef = useRef();

  useEffect(() => {
    var twilioScript = document.createElement('script');  
    twilioScript.setAttribute('src','https://media.twiliocdn.com/sdk/js/conversations/releases/1.2.3/twilio-conversations.min.js');
    twilioScript.setAttribute('integrity','sha256-fGJzzkCU7845NUd6g0dG6/p0vm5aAIIAi0Vmsply9II=');
    twilioScript.setAttribute('crossorigin','anonymous');
    document.head.appendChild(twilioScript);
  }, []);

  useEffect(() => {
    const getAccessTokenAsync = async () => {
      try {
        const res = await getAccessToken();
        setChat(res.data);
      } catch (e) {
        console.log(`>>> Fetching Twilio access token failed.\n${e}`);
        setStatus(null);
      }
    };

    if (chatWindowOpen && status === WidgetStatus.LOADING) {
      getAccessTokenAsync();
    }
  }, [status, chatWindowOpen]);

  useEffect(() => {
    if (chat) 
      intializeConversationAsync();
  }, [chat]);

  const CustomMessage = (props) => {
    return (
      <>
        {props.messageInput}
        {<EmojiPicker onPick={handleEmojiPick} />}
        {props.sendButton}
      </>
    );
  };

  const handleEmojiPick = (emoji) => {
    const currVal = chatRef.current.newMsgComp.inputEl.value
    chatRef.current.newMsgComp.inputEl.value = currVal + emoji.native;
  }

  const intializeConversationAsync = async () => {
    try {
      initConversation(chat.accessToken, onConversationCreated);
      console.log(">>> initConversation invoked.");
    } catch (e) {
      console.log(`>>> Initializing conversation object failed.\n${e}`);
    }
  }

  const onConversationCreated = async (isSuccess) => {
    if (isSuccess) {
      try {
        const res  = await loadConversation();
        const messages = res.items.map(m => mapToChat(m));
        messageRef.current = messages;
        setMessages(messages);

        setStatus(WidgetStatus.LOADED);
        startMessageAdded(onMessageAdded);
      } catch (e) {
        console.log(e);
      }
    } else {
      setTimeout(() => {
        // Retries to create a Twilio conversation client.
        intializeConversationAsync();
      }, 1000);
    }
  }

  /**
   * The callback function invoked when a new message is added to the conversation.
   * @param {*} message Message added to the active conversation.
   */
  const onMessageAdded = (message) => {
    const messageMorphed = mapToChat(message);
    const updatedMsgList = [...messageRef.current, messageMorphed];
    messageRef.current = updatedMsgList;
    setMessages(updatedMsgList);
  };

  const addNewMessage = async (event) => {
    console.log(">>> New message sent.");
    sendMessage(event.message.text);
  };

  const toggleChatWindow = () => {
    const isOpen = !chatWindowOpen;

    if(isOpen) {
      chatIcRef.current.classList.add('hidden');
      closeIcRef.current.classList.remove('hidden');
    } else {
      chatIcRef.current.classList.remove('hidden');
      closeIcRef.current.classList.add('hidden');
    }

    if (!status) {
      setStatus(WidgetStatus.LOADING);
    }

    setChatWindowOpen(isOpen);
  }

  const renderChat = () => {
    if (status === WidgetStatus.LOADED) {
      return (
        <div className="chat-container" id="chatContainer">
          <ChatHeader name={chat.dentist.name} photoUrl={chat.dentist.picture} />
  
          <Chat
            ref={chatRef}
            user={{ id: chat.clientIdentity }}
            messages={messages}
            onMessageSend={addNewMessage}
            placeholder={"Type a message..."}
            messageBox={CustomMessage}
          />
        </div>
      );
    }

    return (<Loading />);
  }

  return (
    <div className="chat-widget-layout">
      {chatWindowOpen && 
        renderChat()
      }

      <div className="button-container" onClick={toggleChatWindow}>
        <img src={crossIc} className="icon hidden" ref={closeIcRef} />
        <img src={chatIc} className="icon" ref={chatIcRef} />
      </div>
    </div>
  );
}

export default App;
